import { Injectable } from "@angular/core";
import { Subject, BehaviorSubject } from "rxjs";
import { Device } from "app/interfaces/device";
import { RestApiService } from "./rest-api.service";
import { SelectedDevice } from "app/interfaces/selected-device";
import { ContinuousDataService } from "./continuous-data.service";
import { Slave } from "app/interfaces/slave";
import { SetupCapability } from "app/interfaces/setup-capability";
import { AoaParameters, ClAoaScannerParameters, ClAoaConfigParameters, ClAoaParameters } from "app/interfaces/configuration";
import { ConnectionStatus } from "app/interfaces/connection-status";
import { ErrorType } from "app/enums/error-type.enum";
import { SlaveListService } from "./slave-list.service";
import { SupportedFeatures } from 'app/enums/supported-features.enum';

@Injectable({
	providedIn: "root"
})
export class RtlsUtilService {
	public errorOccur: Subject<ErrorType> = new Subject<ErrorType>();
	public msgForSpinner: Subject<string> = new Subject<string>();

	public deviceList: Subject<SelectedDevice[]> = new Subject<SelectedDevice[]>();
	public slaveList: Subject<Slave[]> = new Subject<Slave[]>();
	public setupCapability: Subject<SetupCapability> = new Subject<SetupCapability>();
	public connectionStatus: BehaviorSubject<ConnectionStatus> = new BehaviorSubject<ConnectionStatus>({ slave: null, success: false, connection_handle: -1 });
	public cciEnabled: Subject<boolean> = new Subject<boolean>();
	public hadmEnabled: Subject<boolean> = new Subject<boolean>();
	public aoaEnabled: Subject<boolean> = new Subject<boolean>();
	public initialize_done: Subject<boolean> = new Subject<boolean>();
	public inSyncProcess: Subject<boolean> = new Subject<boolean>();

	public logMessages: Subject<string> = new Subject<string>();
	public internalLogMessages: Subject<string> = new Subject<string>();

	private autoPlay: boolean = false;

	constructor(
		private restAPI: RestApiService,
		private continuousData: ContinuousDataService,
		private slaveListService: SlaveListService
	) {
		this.continuousData.isConnected().subscribe(event => {
			if (event == "connected") {
				console.log("Log box subscribe to flow data");
				this.continuousData.data.subscribe(
					recv_data => {
						let result_msg_ids = [
							"RTLS_EVT_CONN_INFO",
							"RTLS_CMD_HADM_RESULT_DIST",
							"RTLS_CMD_AOA_RESULT_ANGLE",
							"RTLS_EVT_PERIODIC_ADV_RPT",
							"RTLS_CMD_CL_AOA_RESULT_ANGLE"
						];

						if (
							!result_msg_ids.some(
								id => id === recv_data.message.command
							)
						) {
							this.logMessages.next(recv_data);
						}
					},
					error => {
						// console.log(error);
						this.errorOccur.next(ErrorType.WebSocketError);
					}
				);
			}
		});
	}

	handle_rest_api_error(msg) {
		if (msg.status >= 500) {
			this.errorOccur.next(ErrorType.ServerActionError);
		}

		if (msg.status === 0) {
			this.errorOccur.next(ErrorType.ServerCommunicationError);
		}
	}

	initialize(device_list: Device[], websocket_port: number, rtlsutil_timeout: number) {
		return new Promise(resolve => {
			this.msgForSpinner.next("Initializing ...");
			this.restAPI.initializeRtlsUtil(device_list, websocket_port, rtlsutil_timeout).subscribe(
				deviceList => {
					this.deviceList.next(deviceList);
					this.continuousData.connect(websocket_port);

					this.slaveListService.clear_all_slave_in_view();
					this.slaveListService.clear_all_slave_connection_status();
					this.slaveListService.clear_all_slave_cci_started_status();
					this.slaveListService.clear_all_slave_aoa_started_status();
					this.slaveListService.clear_all_slave_hadm_started_status();
					this.slaveListService.clear_all_slave_padv_report_started_status();
					this.slaveListService.clear_all_slave_cl_aoa_started_status();

					this.slaveListService.clear_current_slave_list();
					this.slaveListService.clear_periodic_adv_slave_list();

					this.in_sync_process(false);

					this.initialize_done.next(true);
					resolve(deviceList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	finalize() {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Finalizing ...");
			this.restAPI.finalizeRtlsUtil().subscribe(
				() => {
					this.continuousData.disconnect();
					resolve("ok");
				},
				error => {
					this.handle_rest_api_error(error);
					reject("error");
				}
			);
		});
	}

	close_websocket() {
		this.continuousData.disconnect();
	}

	async restart(device_list: Device[], websocket_port: number, rtlsutil_timeout: number) {
		var status = await this.finalize();
		var devices = await this.initialize(device_list, websocket_port, rtlsutil_timeout);
		var setupCapability = await this.get_setup_capability();
	}

	get_setup_capability() {
		return new Promise<SetupCapability>(resolve => {
			this.msgForSpinner.next("Getting Setup Capability ...");
			this.restAPI.getSetupCapability().subscribe(
				setup_capability => {
					this.setupCapability.next(setup_capability);
					this.cciEnabled.next(false);
					this.hadmEnabled.next(false);
					this.aoaEnabled.next(false);

					this.msgForSpinner.next(null);
					resolve(setup_capability);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	get_device_list(feature) {
		return new Promise<Device[]>(resolve => {
			// console.log(this.msgForSpinner);
			this.msgForSpinner.next("Getting Device List ...");

			this.restAPI.getDeviceList(feature).subscribe(
				deviceList => {
					this.msgForSpinner.next(null);
					resolve(deviceList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	indentify_devices(device_list: Device[], feature) {
		return new Promise<Device[]>(resolve => {
			this.msgForSpinner.next("Identifying Devices ...");

			this.restAPI.indentifyDevices(device_list, feature).subscribe(
				deviceList => {
					this.msgForSpinner.next(null);
					resolve(deviceList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	scan(scan_time: number, selected_feature: string) {
		return new Promise<Slave[]>(resolve => {
			this.msgForSpinner.next(`Scanning (${scan_time} sec) ...`);

			let current_connected_slave_list = this.slaveListService.get_connected_slaves();
			let current_synced_slave_list = this.slaveListService.get_synced_slaves();
			let current_periodic_adv_list = this.slaveListService.get_current_periodic_adv_list();

			this.restAPI.scan(scan_time).subscribe(
				slaveList => {
					if (selected_feature !== SupportedFeatures.CL_AOA) {
						slaveList = slaveList.filter((v, i, a) => a.findIndex(t => (t.bd_address === v.bd_address)) === i)
					}

					this.slaveListService.update_slave_list(
						slaveList,
						current_connected_slave_list,
						current_synced_slave_list,
						current_periodic_adv_list
					);

					this.msgForSpinner.next(null);
					resolve(slaveList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	connect(slave: Slave, connection_interval: number) {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Connecting to ${slave.bd_address} ...`);

			this.restAPI.connect(slave, connection_interval).subscribe(
				ret => {
					this.slaveListService.update_slave_connection_status(
						slave,
						ret
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	onConnectionStatusChange() {
		return this.connectionStatus.share();
	}

	disconnect(slave: Slave) {
		return new Promise(resolve => {
			this.msgForSpinner.next("Disconnecting ...");

			this.restAPI.disconnect(slave).subscribe(
				ret => {
					ret.success = !ret.success;
					this.slaveListService.update_slave_connection_status(
						slave,
						ret
					);

					this.slaveListService.clear_slave_in_view(slave);
					this.slaveListService.update_slave_cci_status(slave, false);
					this.slaveListService.update_slave_hadm_status(slave, false);
					this.slaveListService.update_slave_aoa_status(slave, false);

					this.msgForSpinner.next(null);
					resolve({});
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	async diconnect_all_slaves() {
		for (const s of this.slaveListService.get_connected_slaves()) {
			await this.disconnect(s);
		}
	}

	enable_cci(slave: Slave) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Starting CCI ...");

			this.restAPI.enable_cci(slave).subscribe(
				ret => {
					this.slaveListService.update_slave_cci_status(
						slave,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}

	disable_cci(slave: Slave) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Disabling CCI ...");

			this.restAPI.disable_cci(slave).subscribe(
				ret => {
					this.slaveListService.update_slave_cci_status(
						slave,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}

	async disable_all_cci() {
		for (const s of this.slaveListService.get_cci_started_slaves()) {
			await this.disable_cci(s);
		}
	}

	enable_aoa(slave: Slave, aoa_parameters: AoaParameters) {
		return new Promise(resolve => {
			this.msgForSpinner.next(
				"Setting AoA Parameters and Starting AoA ..."
			);

			this.restAPI.enable_aoa(slave, aoa_parameters).subscribe(
				ret => {
					this.slaveListService.update_slave_aoa_status(
						slave,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	disable_aoa(slave: Slave) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Disabling AoA ...");

			this.restAPI.disable_aoa(slave).subscribe(
				ret => {
					this.slaveListService.update_slave_aoa_status(
						slave,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}

	async disable_all_aoa() {
		for (const s of this.slaveListService.get_aoa_started_slaves()) {
			await this.disable_aoa(s);
		}
	}

	sleep(ms) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Sleep for ${ms} [mSec] ...`);
			setTimeout(() => {
				this.msgForSpinner.next(null);
				resolve("OK");
			}, ms);
		});
	}

	async auto_play(
		active_feature: string,
		device_list: Device[],
		websocket_port: number,
		rtlsutil_timeout: number,
		scan_time: number,
		connection_interval: number,
		// hadm_parameters: HadmParameters,
		aoa_parameters: AoaParameters,
		cl_aoa_parameters: ClAoaParameters
	) {
		let connectionSuccess = false;
		let syncSuccess = false;

		let status = await this.finalize();
		let devices = await this.initialize(
			device_list,
			websocket_port,
			rtlsutil_timeout
		);
		let setupCapability = await this.get_setup_capability();
		let slaveList = await this.scan(scan_time, active_feature);

		let bestSlave: Slave;

		if ([SupportedFeatures.AOA].indexOf(active_feature as SupportedFeatures) !== -1) {
			bestSlave = slaveList.reduce((prev, current) => prev.rssi > current.rssi ? prev : current);
			// if (slaveList.length > 1) {
			// } else {
			// 	bestSlave = slaveList[0];
			// }

			let connectionSuccess = await this.connect(bestSlave, connection_interval);

			if (connectionSuccess) {
				this.slaveListService.set_slave_in_view(bestSlave);
			}
		}

		if (active_feature === SupportedFeatures.CL_AOA) {
			let cl_aoa_available_slaves = slaveList.filter(s => s.support_cl_aoa);

			bestSlave = cl_aoa_available_slaves.reduce((prev, current) => prev.rssi > current.rssi ? prev : current);

			cl_aoa_parameters.cl_aoa_scanner.use_periodic_adv_list = false;
			await this.sync(bestSlave, cl_aoa_parameters.cl_aoa_scanner);

			await this.sleep(2000);

			let synced_slave = this.slaveListService.get_synced_slaves();
			if (synced_slave.length === 0) {
				await this.term_sync(bestSlave);

				cl_aoa_parameters.cl_aoa_scanner.use_periodic_adv_list = false;
				await this.sync(bestSlave, cl_aoa_parameters.cl_aoa_scanner);

				await this.sleep(2000);

				synced_slave = this.slaveListService.get_synced_slaves();
			}

			syncSuccess = synced_slave.length > 0 ? true : false;
		}

		if (connectionSuccess && setupCapability.cci_supported) {
			var cciStarted = await this.enable_cci(bestSlave);
			await this.sleep(2000);
		}


		if (active_feature === SupportedFeatures.AOA && connectionSuccess && setupCapability.aoa_supported) {
			var aoaStarted = await this.enable_aoa(bestSlave, aoa_parameters);
			await this.sleep(2000);
		}

		if (active_feature === SupportedFeatures.CL_AOA && syncSuccess) {
			await this.enable_periodic_adv_report(bestSlave);

			await this.sleep(2000);

			await this.enable_cl_aoa(bestSlave, cl_aoa_parameters.cl_aoa_config);
		}
	}

	update_device_list(deviceList: SelectedDevice[]) {
		this.deviceList.next(deviceList);
	}

	getCurrentConnectionStatus(): ConnectionStatus {
		return this.connectionStatus.getValue();
	}

	in_sync_process(status) {
		this.inSyncProcess.next(status);
	}

	sync(slave: Slave, scanner_options: ClAoaScannerParameters) {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Syncing with ${slave.bd_address} ...`);

			this.in_sync_process(true);

			this.restAPI.sync(slave, scanner_options).subscribe(
				ret => {
					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			)
		});
	}

	cancel_sync() {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Canceling sync ...`);

			this.restAPI.cancel_sync().subscribe(
				ret => {
					this.in_sync_process(false);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			)
		});
	}

	term_sync(slave: Slave) {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Terminating Sync ...`);

			this.restAPI.term_sync(slave).subscribe(
				ret => {
					ret.success = !ret.success;
					this.slaveListService.update_slave_sync_status(slave, ret);
					this.slaveListService.update_padvList_slave_sync_status(slave, ret);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			)

		});
	}

	async term_all_sync() {
		for (const s of this.slaveListService.get_synced_slaves()) {
			await this.term_sync(s);
		}
	}

	clear_periodic_adv_list() {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Clear Periodic Advertiser List ...`);

			this.restAPI.clear_periodic_adv_list().subscribe(
				ret => {
					if (ret.success) {
						this.slaveListService.clear_periodic_adv_slave_list();
					}

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	add_slave_into_periodic_adv_list(slave: Slave) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Add ${slave.bd_address} into Periodic Advertiser List ...`);

			this.restAPI.add_slave_into_periodic_adv_list(slave).subscribe(
				ret => {
					if (ret.success) {
						this.slaveListService.add_slave_into_periodic_adv_slave_list(slave);
					}

					this.msgForSpinner.next(null);
					resolve("OK");
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	remove_slave_from_periodic_adv_list(slave: Slave) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Remove ${slave.bd_address} from Periodic Advertiser List ...`);

			this.restAPI.remove_slave_from_periodic_adv_list(slave).subscribe(
				ret => {
					if (ret.success) {
						this.slaveListService.remove_slave_from_periodic_adv_slave_list(slave);
					}

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	enable_periodic_adv_report(slave: Slave) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Enabling periodic advertiser report ...`);

			this.restAPI.enable_periodic_adv_report(slave).subscribe(
				ret => {
					this.slaveListService.update_slave_padv_report_status(
						slave,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	disable_periodic_adv_report(slave: Slave) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Disabling periodic advertiser report ...`);

			this.restAPI.disable_periodic_adv_report(slave).subscribe(
				ret => {
					this.slaveListService.update_slave_padv_report_status(
						slave,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	enable_cl_aoa(slave: Slave, cl_aoa_config_parameters: ClAoaConfigParameters) {
		return new Promise(resolve => {
			this.msgForSpinner.next("Starting CL AoA ...");

			this.restAPI.enable_cl_aoa(slave, cl_aoa_config_parameters).subscribe(
				ret => {
					this.slaveListService.update_slave_cl_aoa_status(
						slave,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	disable_cl_aoa(slave: Slave, cl_aoa_config_parameters: ClAoaConfigParameters) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Disabling CL AoA ...");

			this.restAPI.disable_cl_aoa(slave, cl_aoa_config_parameters).subscribe(
				ret => {
					this.slaveListService.update_slave_cl_aoa_status(
						slave,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}

	async disable_all_cl_aoa(cl_aoa_config_parameters: ClAoaConfigParameters) {
		for (const s of this.slaveListService.get_cl_aoa_started_slaves()) {
			await this.disable_cl_aoa(s, cl_aoa_config_parameters);
		}
	}
}
