import { Component, OnInit, OnDestroy, AfterViewInit } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MatDialog } from "@angular/material";
import { ConfigurationDialogComponent } from "app/dialogs/configuration-dialog/configuration-dialog.component";
import { Configuration } from "app/interfaces/configuration";
import { ConfigurationService } from "app/services/configuration.service";
import { RtlsUtilService } from "app/services/rtls-util.service";
import { ToastrService } from "ngx-toastr";
import { ErrorDialogComponent } from "app/dialogs/error-dialog/error-dialog.component";
import { ErrorType } from "app/enums/error-type.enum";
import { error } from "util";
import { SelectedDevice } from "app/interfaces/selected-device";
import { SlaveListService } from "app/services/slave-list.service";
import { ActivatedRoute, Router } from "@angular/router";
import { ConfigurationType } from 'app/enums/configuration-type.enum';
import { SupportedFeatures } from 'app/enums/supported-features.enum';

@Component({
	selector: "app-dashboard",
	templateUrl: "./dashboard.component.html",
	styleUrls: ["./dashboard.component.scss"],
})
export class DashboardComponent implements OnInit, OnDestroy, AfterViewInit {
	selected_devices: SelectedDevice[] = [];

	spinner_message = "Loading ...";

	_configuration: Configuration;

	is_general_error: boolean = false;

	is_one_of_features_is_active: boolean = false;
	features_state = {
		cciEnabled: false,
		hadmEnabled: false,
		aoaEnabled: false,
		periodicReportEnabled: false,
		clAoaEnabled: false
	};

	public supportedFeatures = SupportedFeatures;
	public selectedFeature = null;

	private configurationServiceOnConfigurationChangeSubscribeObj: any;

	private rtlsUtilServiceErrorOccurSubscribeObj: any;
	private rtlsUtilServiceDeviceListSubscribeObj: any;
	private rtlsUtilServiceMsgForSpinnerSubscribe: any;

	private slaveListServiceIsAnySlaveCciStartedSubscribeObj: any;
	private slaveListServiceIsAnySlaveHadmStartedSubscribeObj: any;
	private slaveListServiceIsAnySlaveAoaStartedSubscribeObj: any;
	private slaveListServiceIsAnySlavePeriodicReportStartedSubscribeObj: any;
	private slaveListServiceIsAnySlaveClAoaStartedSubscribeObj: any;

	constructor(
		private router: Router,
		private route: ActivatedRoute,
		private spinner: NgxSpinnerService,
		public dialog: MatDialog,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService,
		private slaveListService: SlaveListService
	) { }

	async ngOnInit() {
		this.route.paramMap.subscribe((params) => {
			this.selectedFeature = params.get("feature");
		});


		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService
			.onConfigurationChange()
			.subscribe((new_configuration) => {
				this._configuration = new_configuration;
			});

		this.rtlsUtilServiceErrorOccurSubscribeObj = this.rtlsUtilService.errorOccur.subscribe(
			(errorType) => {
				console.log(errorType);

				this.spinner.hide();
				this.is_general_error = errorType === ErrorType.WebSocketError;

				const dialogRef = this.dialog.open(ErrorDialogComponent, {
					width: "500px",
					height: "auto",
					disableClose: true,
					data: { error_type: errorType },
				});
			}
		);

		this.rtlsUtilServiceDeviceListSubscribeObj = this.rtlsUtilService.deviceList.subscribe(
			(deviceList) => {
				this.selected_devices = deviceList;
				this.is_general_error = false;
			}
		);

		this.rtlsUtilServiceMsgForSpinnerSubscribe = this.rtlsUtilService.msgForSpinner.subscribe(
			(msg) => {
				if (msg) {
					this.spinner_message = msg;
					this.spinner.show();
				} else {
					this.spinner.hide();
				}
			}
		);

		this.slaveListServiceIsAnySlaveCciStartedSubscribeObj = this.slaveListService.isAnySlaveCciStarted.subscribe(
			(anyCciEnabled) => {
				this.features_state.cciEnabled = anyCciEnabled;
				this.enable_disable_configurations();
			}
		);

		this.slaveListServiceIsAnySlaveHadmStartedSubscribeObj = this.slaveListService.isAnySlaveHadmStarted.subscribe(
			(anyHadmEnabled) => {
				this.features_state.hadmEnabled = anyHadmEnabled;
				this.enable_disable_configurations();
			}
		);

		this.slaveListServiceIsAnySlaveAoaStartedSubscribeObj = this.slaveListService.isAnySlaveAoaStarted.subscribe(
			(anyAoaEnabled) => {
				this.features_state.aoaEnabled = anyAoaEnabled;
				this.enable_disable_configurations();
			}
		);

		this.slaveListServiceIsAnySlavePeriodicReportStartedSubscribeObj = this.slaveListService.isAnySlavePadvReportStarted.subscribe(
			(anyPeriodicReportEnabled) => {
				this.features_state.periodicReportEnabled = anyPeriodicReportEnabled;
				this.enable_disable_configurations();
			}
		);

		this.slaveListServiceIsAnySlaveClAoaStartedSubscribeObj = this.slaveListService.isAnySlaveClAoaStarted.subscribe(
			(anyClAoaEnabled) => {
				this.features_state.clAoaEnabled = anyClAoaEnabled;
				this.enable_disable_configurations();
			}
		);

		let device_list = JSON.parse(
			localStorage.getItem("selectedDevicesList")
		);

		await this.configurationService.getConfigurationFromServer();
		await this.rtlsUtilService.initialize(
			device_list,
			this._configuration.general.websocket_port,
			this._configuration.general.rtls_util_timeout
		);
		await this.rtlsUtilService.get_setup_capability();
	}

	ngAfterViewInit() { }

	ngOnDestroy() {
		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();

		this.rtlsUtilServiceErrorOccurSubscribeObj.unsubscribe();
		this.rtlsUtilServiceDeviceListSubscribeObj.unsubscribe();
		this.rtlsUtilServiceMsgForSpinnerSubscribe.unsubscribe();

		this.slaveListServiceIsAnySlaveCciStartedSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlaveHadmStartedSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlaveAoaStartedSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlavePeriodicReportStartedSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlaveClAoaStartedSubscribeObj.unsubscribe();

		this.rtlsUtilService.close_websocket();
	}

	auto_start() {
		if (!this.is_general_error) {
			console.debug("auto_start");

			let device_list = JSON.parse(localStorage.getItem("selectedDevicesList"));
			this.rtlsUtilService.auto_play(
				this.selectedFeature,
				device_list,
				this._configuration.general.websocket_port,
				this._configuration.general.rtls_util_timeout,
				this._configuration.scan_connection.scan_time,
				this._configuration.scan_connection.connection_interval,
				this._configuration.aoa_parameters,
				this._configuration.cl_aoa_parameters
			);
		}
	}

	async restart() {
		let device_list = JSON.parse(
			localStorage.getItem("selectedDevicesList")
		);

		await this.rtlsUtilService.restart(device_list, this._configuration.general.websocket_port, this._configuration.general.rtls_util_timeout);
	}

	enable_disable_configurations() {
		this.is_one_of_features_is_active =
			this.features_state.cciEnabled ||
			this.features_state.hadmEnabled ||
			this.features_state.aoaEnabled ||
			this.features_state.periodicReportEnabled ||
			this.features_state.clAoaEnabled;
	}

	isColorChanged(oldColors: string[], newColors: string[]): boolean {
		if (oldColors.length != newColors.length) {
			return true;
		}

		for (let index = 0; index < oldColors.length; index++) {
			if (oldColors[index] != newColors[index]) {
				return true;
			}
		}

		return false;
	}

	configurations() {
		if (!this.is_one_of_features_is_active) {
			let last_configuration: Configuration = JSON.parse(JSON.stringify(this.configurationService.getCurrentConfiguration()));

			const dialogRef = this.dialog.open(ConfigurationDialogComponent, {
				width: "900px",
				height: "auto",
				disableClose: true,
				data: {
					configuration: JSON.parse(
						JSON.stringify(
							this.configurationService.getCurrentConfiguration()
						)
					),
					active_feature: this.selectedFeature,
					selected_tab: -1,
				},
			});

			dialogRef.afterClosed().subscribe((result) => {
				console.log("The dialog was closed", result);
				if (result !== undefined) {
					if (this.isColorChanged(last_configuration.general.color_schema, result.general.color_schema)) {
						this.selected_devices.forEach((device, index) => {
							device.graph_color =
								result.general.color_schema[index];
						});

						this.rtlsUtilService.update_device_list(
							this.selected_devices
						);
					}

					let configType = null;
					switch (this.selectedFeature) {
						case SupportedFeatures.AOA:
							configType = ConfigurationType.AOA;
							break;
						case SupportedFeatures.CL_AOA:
							configType = ConfigurationType.CL_AOA;
							break;
						default:
							configType = ConfigurationType.NONE;
							break;
					}

					this.configurationService.setConfiguration(result, ConfigurationType.ALL | configType);
				}
			});
		}
	}

	home() {
		this.rtlsUtilService.finalize().then(() => {
			this.router.navigate(["/home-page"]);
		});
	}
}
