import {
	Component,
	OnInit,
	Input,
	ViewChild,
	AfterViewInit,
	OnDestroy,
	ViewEncapsulation,
} from "@angular/core";
import { MatTableDataSource, MatSort, MatSortable } from "@angular/material";
import { Slave } from "app/interfaces/slave";
// import { SelectionModel } from '@angular/cdk/collections';
import { RestApiService } from "app/services/rest-api.service";
import { Configuration } from "app/interfaces/configuration";
import { ConfigurationService } from "app/services/configuration.service";
import { RtlsUtilService } from "app/services/rtls-util.service";
import { SetupCapability } from "app/interfaces/setup-capability";
import { ConnectionStatus } from "app/interfaces/connection-status";
import { SlaveListService } from "app/services/slave-list.service";
import { SelectionModel } from "@angular/cdk/collections";
import { ToastrService } from "ngx-toastr";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/interval";
import "rxjs/add/operator/takeUntil";
import { Subject } from "rxjs";
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { P } from 'mat-table-exporter';

@Component({
	selector: "slave-list",
	templateUrl: "./slave-list.component.html",
	styleUrls: ["./slave-list.component.scss"],
	// encapsulation: ViewEncapsulation.None,
})
export class SlaveListComponent implements OnInit, OnDestroy {
	@Input() error_occur: boolean = false;

	@Input() selectedFeature: string;

	@ViewChild(MatSort, { static: true }) sort: MatSort;

	private stopHadmInterval$ = new Subject<void>();

	private _configuration: Configuration;

	private setup_capability: SetupCapability = {
		multi_connection_supported: false,
		cci_supported: false,
		aoa_supported: false,
		hadm_supported: false,
	};

	public supportedFeatures = SupportedFeatures;

	MAX_SLAVE = 8;
	enable_scan = true;
	any_slave_connected = false;
	number_of_connected_slave = 0;
	any_cci_started = false;
	any_aoa_started = false;
	any_hadm_started = false;
	public in_sync_process = false;

	dataSource = new MatTableDataSource<Slave>([]);

	private configurationServiceOnConfigurationChangeSubscribeObj: any;
	private slaveListServiceOnSlaveListChangeSubscribeObj: any;
	private slaveListServiceIsAnySlaveConnectedSubscribeObj: any;
	private slaveListServiceIsAnySlaveCciStartedSubscribeObj: any;
	private slaveListServiceIsAnySlaveAoaStartedSubscribeObj: any;
	private slaveListServiceIsAnySlaveHadmStartedSubscribeObj: any;
	private rtlsUtilServiceSetupCapabilitySubscribeObj: any;

	constructor(
		private toastr: ToastrService,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService,
		private slaveListService: SlaveListService
	) { }

	ngOnInit() {
		console.log("Slave List Init");

		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService.onConfigurationChange().subscribe((configuration) => {
			this._configuration = configuration;
			console.log("Configuration updated in slave component");
		});
		this.slaveListServiceOnSlaveListChangeSubscribeObj = this.slaveListService.on_slave_list_change().subscribe((slaveList) => {
			this.dataSource.data = slaveList;

			this.enable_scan = true;
			let connected_slaves_length = this.slaveListService.get_connected_slaves().length;
			if (connected_slaves_length > 0) {
				this.enable_scan = connected_slaves_length < (this.setup_capability.multi_connection_supported ? this.MAX_SLAVE : 1);
			}
		});
		this.slaveListServiceIsAnySlaveConnectedSubscribeObj = this.slaveListService.isAnySlaveConnected.subscribe(
			(status) => (this.any_slave_connected = status)
		);
		this.slaveListServiceIsAnySlaveCciStartedSubscribeObj = this.slaveListService.isAnySlaveCciStarted.subscribe(
			(status) => (this.any_cci_started = status)
		);
		this.slaveListServiceIsAnySlaveAoaStartedSubscribeObj = this.slaveListService.isAnySlaveAoaStarted.subscribe(
			(status) => (this.any_aoa_started = status)
		);
		this.slaveListServiceIsAnySlaveHadmStartedSubscribeObj = this.slaveListService.isAnySlaveHadmStarted.subscribe(
			(status) => (this.any_hadm_started = status)
		);
		this.rtlsUtilServiceSetupCapabilitySubscribeObj = this.rtlsUtilService.setupCapability.subscribe(
			(setupCapability) => {
				this.setup_capability = setupCapability;
			}
		);
		this.rtlsUtilService.inSyncProcess.subscribe(in_process => {
			this.in_sync_process = in_process;
		})

		this.dataSource.sort = this.sort;
		this.dataSource.sortingDataAccessor = (data, attribute) => {
			switch (attribute) {
				case "rssi":
					return Math.abs(data[attribute]);
				default:
					return data[attribute];
			}
		};
		this.dataSource.sort.sort(<MatSortable>{
			id: "rssi",
			start: "asc",
		});
	}

	ngOnDestroy() {
		console.log("Slave List Destroy")

		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.slaveListServiceOnSlaveListChangeSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlaveConnectedSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlaveCciStartedSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlaveAoaStartedSubscribeObj.unsubscribe();
		this.slaveListServiceIsAnySlaveHadmStartedSubscribeObj.unsubscribe();
		this.rtlsUtilServiceSetupCapabilitySubscribeObj.unsubscribe();
	}

	sortChange(e) {
		if (e.direction !== "") {
			this.dataSource.sortData(
				this.dataSource.filteredData,
				this.dataSource.sort
			);
		}
	}

	scan_for_slave() {
		this.rtlsUtilService.scan(this._configuration.scan_connection.scan_time, this.selectedFeature);
	}

	async connection(row) {
		if ("is_connected" in row && row.is_connected) {
			let row_was_in_view = row.in_view;

			if (this.stopHadmInterval$) {
				this.stopHadmInterval$.next();
			}

			await this.rtlsUtilService.disconnect(row);

			if (row_was_in_view && this.any_slave_connected) {
				let connected_slaves = this.slaveListService.get_connected_slaves();
				this.slaveListService.set_slave_in_view(connected_slaves[0]);
			}
		} else {
			let current_connected_slaves_num = this.slaveListService.get_connected_slaves().length;

			if (current_connected_slaves_num === 1 && !this.setup_capability.multi_connection_supported) {
				this.toastr.info("Current Device not Supporting Multi Connection", "Multi Connection Not Supported", {
					timeOut: 3000,
					positionClass: "toast-bottom-full-width",
				});

				return;
			}

			// Retrun with new HADM
			// if ( (current_connected_slaves_num + 1) > 1 && this.any_tof_started) {
			// 	this.disable_all_tof();
			// }

			await this.rtlsUtilService.connect(row, this._configuration.scan_connection.connection_interval);

			if (this.slaveListService.get_connected_slaves().length == 1) {
				this.slaveListService.set_slave_in_view(row);
			}
		}

		let connected_slaves_length = this.slaveListService.get_connected_slaves().length;
		this.number_of_connected_slave = connected_slaves_length;
		this.enable_scan = connected_slaves_length < (this.setup_capability.multi_connection_supported ? this.MAX_SLAVE : 1);
	}

	disconnect_all_slave() {
		this.rtlsUtilService.diconnect_all_slaves();

		let connected_slaves_length = this.slaveListService.get_connected_slaves().length;
		this.number_of_connected_slave = connected_slaves_length;
		this.enable_scan = connected_slaves_length < (this.setup_capability.multi_connection_supported ? this.MAX_SLAVE : 1);
	}

	cci(row) {
		if ("is_connected" in row && row.is_connected) {
			if ("cci_started" in row && row.cci_started) {
				this.rtlsUtilService.disable_cci(row);
			} else {
				this.rtlsUtilService.enable_cci(row);
			}
		}
	}

	disable_all_cci() {
		this.rtlsUtilService.disable_all_cci();
	}

	aoa(row) {
		if ("is_connected" in row && row.is_connected) {
			if ("aoa_started" in row && row.aoa_started) {
				this.rtlsUtilService.disable_aoa(row);
			} else {
				this.rtlsUtilService.enable_aoa(
					row,
					this._configuration.aoa_parameters
				);
			}
		}
	}

	disable_all_aoa() {
		this.rtlsUtilService.disable_all_aoa();
	}

	update_view_selection(e, row) {
		e.preventDefault();

		if (row.is_connected) {
			this.slaveListService.set_slave_in_view(row);
		}
	}

	getDisplayedColumns(): string[] {
		let displayedColumnsHADM = [
			"select",
			"bd_address",
			"rtls_role",
			"rssi",
			"aConnect",
			"aCCI",
			"aHADM",
		];
		let displayedColumnsAOA = [
			"select",
			"bd_address",
			"rtls_role",
			"rssi",
			"aConnect",
			"aCCI",
			"aAoA",
		];
		let displayedColumnsCLAOA = [
			"aAddToPerAdvList",
			// "select",
			"bd_address",
			// "rtls_role",
			"rssi",
			"aConnectionHandle",
			"aPeriodicInterval",
			"aSync",
			"aPerdiocAdvertiserReport",
			"aClAoA",
			"aSyncColor"
		];

		switch (this.selectedFeature) {
			case SupportedFeatures.AOA:
				return displayedColumnsAOA
				break;
			case SupportedFeatures.CL_AOA:
				return displayedColumnsCLAOA
				break;
			default:
				break;
		}
	}

	async sync(row) {
		if ("is_synced" in row && row.is_synced) {
			await this.rtlsUtilService.term_sync(row);
		} else {
			this._configuration.cl_aoa_parameters.cl_aoa_scanner.use_periodic_adv_list = false;
			await this.rtlsUtilService.sync(row, this._configuration.cl_aoa_parameters.cl_aoa_scanner);
		}
	}

	cancel_sync() {
		this.rtlsUtilService.cancel_sync();
	}

	add_to_per_adv_list(row) {
		this.rtlsUtilService.add_slave_into_periodic_adv_list(row);
	}

	perdioc_adv_report(row) {
		if (!('padv_report_started' in row) || row.padv_report_started === false) {
			this.rtlsUtilService.enable_periodic_adv_report(row);
		} else {
			this.rtlsUtilService.disable_periodic_adv_report(row);
		}
	}

	cl_aoa(row) {
		if ("is_synced" in row && row.is_synced) {
			if ("cl_aoa_started" in row && row.cl_aoa_started) {
				this.rtlsUtilService.disable_cl_aoa(
					row,
					this._configuration.cl_aoa_parameters.cl_aoa_config
				);
			} else {
				this.rtlsUtilService.enable_cl_aoa(
					row,
					this._configuration.cl_aoa_parameters.cl_aoa_config
				);
			}
		}
	}

	disable_all_cl_aoa() {
		this.rtlsUtilService.disable_all_cl_aoa(this._configuration.cl_aoa_parameters.cl_aoa_config);
	}
}
