import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Device } from "app/interfaces/device";
import { Observable } from "rxjs";
import { SelectedDevice } from "app/interfaces/selected-device";
import { Slave } from "app/interfaces/slave";
import { AoaParameters, Configuration, ClAoaScannerParameters, ClAoaConfigParameters } from "app/interfaces/configuration";
import { SetupCapability } from "app/interfaces/setup-capability";
import { environment } from "../../environments/environment";

@Injectable({
	providedIn: "root"
})
export class RestApiService {
	private env = environment;

	private SERVER_IP: string = this.env.server_ip;
	private SERVER_PORT: number = this.env.server_port;

	constructor(private http: HttpClient) { }

	getAppVersion(): Observable<any> {
		return this.http.get<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/appVersion`
		);
	}

	getWarningMessages(): Observable<any> {
		return this.http.get<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/warnings`);
	}

	getConfiguration(version: number): Observable<Configuration> {
		return this.http.put<Configuration>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/configuration`, JSON.stringify({ version: version }));
	}

	setConfiguration(v: Configuration): Observable<any> {
		return this.http.post<Configuration>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/configuration`,
			JSON.stringify(v)
		);
	}

	getDeviceList(feature): Observable<Device[]> {
		return this.http.get<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/devices-${feature}`
		);
	}

	indentifyDevices(deviceList: Device[], feature): Observable<Device[]> {
		return this.http.post<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/devices-${feature}`,
			JSON.stringify(deviceList)
		);
	}

	initializeRtlsUtil(
		deviceList: Device[],
		websocket_port: number,
		rtlsutil_timeout: number
	): Observable<SelectedDevice[]> {
		return this.http.post<SelectedDevice[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`,
			JSON.stringify({
				deviceList: deviceList,
				websocketPort: websocket_port,
				rtlsUtilTimeout: rtlsutil_timeout
			})
		);
	}

	finalizeRtlsUtil(): Observable<any> {
		return this.http.delete<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`
		);
	}

	getSetupCapability(): Observable<SetupCapability> {
		return this.http.get<SetupCapability>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`
		);
	}

	scan(scan_time: Number): Observable<Slave[]> {
		return this.http.post<Slave[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/scan`,
			JSON.stringify({
				scan_time: scan_time
			})
		);
	}

	connect(slave: Slave, connection_interval: Number): Observable<any> {
		return this.http.post<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/connection`,
			JSON.stringify({
				slave: slave,
				connection_interval: connection_interval
			})
		);
	}

	disconnect(slave: Slave): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/connection`,
			JSON.stringify(slave)
		);
	}

	enable_cci(slave: Slave): Observable<any> {
		return this.http.post<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cci`,
			JSON.stringify(slave)
		);
	}

	disable_cci(slave: Slave): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cci`,
			JSON.stringify(slave)
		);
	}

	enable_aoa(slave: Slave, aoa_parameters: AoaParameters): Observable<any> {
		return this.http.post<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/aoa`,
			JSON.stringify({ slave: slave, aoa_parameters: aoa_parameters })
		);
	}

	disable_aoa(slave: Slave): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/aoa`,
			JSON.stringify(slave)
		);
	}

	shutdown(): Observable<any> {
		return this.http.get(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/shutdown`
		);
	}

	sync(slave: Slave, scanner_options: ClAoaScannerParameters): Observable<any> {
		return this.http.post<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/sync`, JSON.stringify({ slave: slave, options: scanner_options }));
	}

	cancel_sync() {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/sync`);
	}

	term_sync(slave: Slave): Observable<any> {
		return this.http.put<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/sync`, JSON.stringify(slave));
	}

	add_slave_into_periodic_adv_list(slave: Slave): Observable<any> {
		return this.http.post<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv-list`, JSON.stringify({ slave: slave }));
	}

	remove_slave_from_periodic_adv_list(slave: Slave): Observable<any> {
		return this.http.put<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv-list`, JSON.stringify({ slave: slave }));
	}

	clear_periodic_adv_list(): Observable<any> {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv-list`);
	}

	enable_periodic_adv_report(slave: Slave): Observable<any> {
		return this.http.post<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv`, JSON.stringify({ slave: slave }));
	}

	disable_periodic_adv_report(slave: Slave): Observable<any> {
		return this.http.put<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv`, JSON.stringify({ slave: slave }));
	}

	enable_cl_aoa(slave: Slave, cl_aoa_config_parameters: ClAoaConfigParameters): Observable<any> {
		return this.http.post<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa`,
			JSON.stringify({ slave: slave, cl_aoa_parameters: cl_aoa_config_parameters })
		);
	}

	disable_cl_aoa(slave: Slave, cl_aoa_config_parameters: ClAoaConfigParameters): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa`,
			JSON.stringify({ slave: slave, cl_aoa_parameters: cl_aoa_config_parameters })
		);
	}

}
