import { Component, OnInit, Inject, ViewEncapsulation, AfterViewInit, } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import { Configuration, AoaBarGraphParameters, } from "app/interfaces/configuration";
import { ConfigurationService } from "app/services/configuration.service";
import { FormControl, Validators } from "@angular/forms";
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { environment } from 'environments/environment'

@Component({
	selector: "configuration-dialog",
	templateUrl: "./configuration-dialog.component.html",
	styleUrls: ["./configuration-dialog.component.scss"],
	encapsulation: ViewEncapsulation.None,
})
export class ConfigurationDialogComponent implements OnInit {
	public supportedFeatures = SupportedFeatures;

	channels = Array<string>(37).fill("").map((v, i) => i.toString());

	yes_no_options = [
		{ value: false, desc: "No" },
		{ value: true, desc: "Yes" }
	];

	not_yes_no_options = [
		{ value: true, desc: "No" },
		{ value: false, desc: "Yes" }
	];

	public AOA_MODE_ANGLE = "AOA_MODE_ANGLE";
	public AOA_MODE_PAIR_ANGLES = "AOA_MODE_PAIR_ANGLES";
	public AOA_MODE_RAW = "AOA_MODE_RAW";

	aoaRunModes = [
		{ mode: this.AOA_MODE_ANGLE, disabled: false },
		{ mode: this.AOA_MODE_PAIR_ANGLES, disabled: true },
		{ mode: this.AOA_MODE_RAW, disabled: false },
	];
	aoaAntList = [0, 1, 2, 3, 4, 5];

	aoaSamplingFilters = [
		{ key: "Default Filtering", value: 0 },
		{ key: "RAW_RF No Filtering", value: 1 },
	];
	cc26x2SelectedAoaSamplingFilter = 0;
	cc2640r2SelectedAoaSamplingFilter = 0;

	aoaSamplingAntennas = [
		// { key: "Both", value: 0 },
		{ key: "Ant 1 Only", value: 16 },
		{ key: "Ant 2 Only", value: 32 },
	];
	aoa_raw_analyze_algos = [
		{ value: 'algo1', desc: "Algo1" },
		{ value: 'algo2', desc: "Algo2" }
	];
	aoa_algos_search_speeds = [
		{ value: 'fast', desc: "Fast" },
		{ value: 'slow', desc: "Slow" }
	];

	cc26x2SelectedAoaSamplingAntenna = 16;
	cc2640r2SelectedAoaSamplingAntenna = 16;

	numberOfMessages = [50, 75, 100, 125, 150];

	hadm_display_modes = [
		{ value: 'simple', desc: "Simple" },
		{ value: 'advanced', desc: "Advanced" }
	];

	hadm_raw_analyze_algos = [
		{
			value: 'bf',
			desc: "Beamforming",
		},
		{
			value: 'music',
			desc: "MSPS MUSIC",
		},
		{
			value: 'phase',
			desc: "Phase Fit",
		},
		{
			value: 'ifft',
			desc: "IFFT",
		}
	];

	hadmRttModes = [
		{ value: 0, desc: "None" },
		{ value: 1, desc: "Coarse" },
		{ value: 2, desc: "Fractional with PN sequence" },
		{ value: 3, desc: "Fractional with Sounding sequence" },
	];

	hadmRttPhys = [
		{ value: 0, desc: "1M" },
		{ value: 1, desc: "2M" },
	];

	clAoaRoles = [
		{ mode: "AOA_MASTER" },
	]
	clAoaRunModes = [
		{ mode: this.AOA_MODE_ANGLE, disabled: false },
		{ mode: this.AOA_MODE_PAIR_ANGLES, disabled: true },
		{ mode: this.AOA_MODE_RAW, disabled: false },
	];
	cl_aoa_sync_cte_types = [
		{ value: 0, desc: "Sync All" },
		{ value: 1, desc: "Don't sync with an AoA CTE" },
		{ value: 2, desc: "Don't sync with an AoD CTE with 1 us slots" },
		{ value: 4, desc: "Don't sync with an AoD CTE with 2 us slots" },
		{ value: 16, desc: "Don't sync with CTE" },
	];
	clAoaSelectedAoaSamplingFilter = 1;
	clAoaSelectedAoaSamplingAntenna = 16;


	configuration: Configuration;
	activeFeature: string = "";
	selectedTab: number = 0;
	disableAll: boolean = false;

	websocketPortFormControl = new FormControl("", [Validators.required, this.portMatchValidator])

	cciGraphScaleStepFormControl = new FormControl("", Validators.min(1));

	aoaGraphAverageEachNPointFormControl = new FormControl("", Validators.min(1));
	aoaGraphNumberOfPointsToDisplayFormControl = new FormControl("", Validators.min(25));
	aoaGraphScaleStepFormControl = new FormControl("", Validators.min(1));
	aoaBarGraphScaleStepFormControl = new FormControl("", Validators.min(1));

	hadmGraphNumberOfPointsToDisplayFormControl = new FormControl("", Validators.min(25));
	hadmGraphAverageEachNPointFormControl = new FormControl("", Validators.min(1));
	hadmGraphScaleStepFormControl = new FormControl("", Validators.min(1));

	clAoaSyncTimeoutFormControl = new FormControl("", [Validators.min(100), Validators.max(163840)]);

	constructor(
		public dialogRef: MatDialogRef<ConfigurationDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private configurationService: ConfigurationService
	) { }

	onNoClick(): void {
		this.dialogRef.close();
	}

	onSave() {
		if (
			!(
				this.aoaGraphAverageEachNPointFormControl.hasError("min") || this.aoaGraphNumberOfPointsToDisplayFormControl.hasError("min")
				|| this.aoaGraphScaleStepFormControl.hasError("min") || this.aoaBarGraphScaleStepFormControl.hasError("min")
				|| this.cciGraphScaleStepFormControl.hasError("min") || this.hadmGraphNumberOfPointsToDisplayFormControl.hasError("min")
				|| this.hadmGraphAverageEachNPointFormControl.hasError("min") || this.hadmGraphScaleStepFormControl.hasError("min")
				|| this.clAoaSyncTimeoutFormControl.hasError("min") || this.clAoaSyncTimeoutFormControl.hasError("max")
				|| this.websocketPortFormControl.hasError('backend_match')
			)
		) {
			this.configuration.general.websocket_port = this.websocketPortFormControl.value;

			this.configuration.general.cci_graph_parameters.scale.step = this.cciGraphScaleStepFormControl.value;

			// this.configuration.general.hadm_graph_parameters.number_of_points_to_display = this.hadmGraphNumberOfPointsToDisplayFormControl.value;
			// this.configuration.general.hadm_graph_parameters.average_each_n_point = this.hadmGraphAverageEachNPointFormControl.value;
			// this.configuration.general.hadm_graph_parameters.scale.step = this.hadmGraphScaleStepFormControl.value;

			this.configuration.general.aoa_graph_parameters.number_of_points_to_display = this.aoaGraphNumberOfPointsToDisplayFormControl.value;
			this.configuration.general.aoa_graph_parameters.average_each_n_point = this.aoaGraphAverageEachNPointFormControl.value;
			this.configuration.general.aoa_graph_parameters.scale.step = this.aoaGraphScaleStepFormControl.value;
			this.configuration.general.aoa_bar_graph_parameters.scale.step = this.aoaBarGraphScaleStepFormControl.value;

			this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control = this.cc26x2SelectedAoaSamplingFilter | this.cc26x2SelectedAoaSamplingAntenna;
			this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control = this.cc2640r2SelectedAoaSamplingFilter | this.cc2640r2SelectedAoaSamplingAntenna;

			this.configuration.cl_aoa_parameters.cl_aoa_scanner.sync_timeout = Math.floor(this.clAoaSyncTimeoutFormControl.value / 10);
			this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_sampling_control = this.clAoaSelectedAoaSamplingFilter | this.clAoaSelectedAoaSamplingAntenna;

			this.dialogRef.close(this.configuration);
		}
	}

	ngOnInit() {
		this.configuration = this.data.configuration;
		this.activeFeature = this.data.active_feature;
		this.selectedTab = this.data.selected_tab;
		this.disableAll = this.data.selected_tab !== -1 ? true : false;

		let default_configuration = this.configurationService.getDefaultConfiguration();

		if (!("color_schema" in this.configuration.general)) {
			this.configuration.general.color_schema = JSON.parse(JSON.stringify(default_configuration.general.color_schema));
		}

		if (!("aoa_bar_graph_parameters" in this.configuration.general)) {
			Object.assign(this.configuration.general, {
				aoa_bar_graph_parameters: JSON.parse(JSON.stringify(default_configuration.general.aoa_bar_graph_parameters)),
			});
		}

		// if (!("hadm_parameters" in this.configuration)) {
		// 	Object.assign(this.configuration, {
		// 		hadm_parameters: JSON.parse(JSON.stringify(default_configuration.hadm_parameters)),
		// 	});
		// }

		// if (!("hadm_graph_parameters" in this.configuration.general)) {
		// 	Object.assign(this.configuration.general, {
		// 		hadm_graph_parameters: JSON.parse(JSON.stringify(default_configuration.general.hadm_graph_parameters)),
		// 	});
		// }

		// if (!('hadm_raw_analyze_algo' in this.configuration.hadm_parameters)) {
		// 	Object.assign(this.configuration.hadm_parameters, {
		// 		hadm_raw_analyze_algo: default_configuration.hadm_parameters.hadm_raw_analyze_algo
		// 	});
		// }

		this.websocketPortFormControl.setValue(this.configuration.general.websocket_port);

		this.cciGraphScaleStepFormControl.setValue(this.configuration.general.cci_graph_parameters.scale.step);

		// this.hadmGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.hadm_graph_parameters.number_of_points_to_display);
		// this.hadmGraphAverageEachNPointFormControl.setValue(this.configuration.general.hadm_graph_parameters.average_each_n_point);
		// this.hadmGraphScaleStepFormControl.setValue(this.configuration.general.hadm_graph_parameters.scale.step);

		this.aoaGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.aoa_graph_parameters.number_of_points_to_display);
		this.aoaGraphAverageEachNPointFormControl.setValue(this.configuration.general.aoa_graph_parameters.average_each_n_point);
		this.aoaGraphScaleStepFormControl.setValue(this.configuration.general.aoa_graph_parameters.scale.step);
		this.aoaBarGraphScaleStepFormControl.setValue(this.configuration.general.aoa_bar_graph_parameters.scale.step);

		this.cc26x2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0x0f;
		this.cc26x2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0xf0;
		this.cc2640r2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0x0f;
		this.cc2640r2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0xf0;

		this.clAoaSyncTimeoutFormControl.setValue(this.configuration.cl_aoa_parameters.cl_aoa_scanner.sync_timeout * 10);
		this.clAoaSelectedAoaSamplingFilter = this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_sampling_control & 0x0f;
		this.clAoaSelectedAoaSamplingAntenna = this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_sampling_control & 0xf0;
	}

	portMatchValidator(control: FormControl) {
		let current_selected_port = control.value;
		let backend_port = environment.server_port;

		if (backend_port === current_selected_port) {
			return {
				backend_match: true
			}
		}

		return null;
	}

	select_channels(start: number, stop: number) {
		if (start !== -1 && stop !== -1) {
			this.configuration.cci_parameters.channels = Array<string>(stop - start + 1).fill("").map((v, i) => (i + start).toString());
		} else {
			this.configuration.cci_parameters.channels = [];
		}
	}

	aoa_select_channels(start: number, stop: number) {
		if (start !== -1 && stop !== -1) {
			this.configuration.general.aoa_bar_graph_parameters.channels = Array<string>(stop - start + 1).fill("").map((v, i) => (i + start).toString());
		} else {
			this.configuration.general.aoa_bar_graph_parameters.channels = [];
		}
	}

	set_default() {
		this.configuration = JSON.parse(JSON.stringify(this.configurationService.getDefaultConfiguration()));

		this.cciGraphScaleStepFormControl.setValue(this.configuration.general.cci_graph_parameters.scale.step);

		// this.hadmGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.hadm_graph_parameters.number_of_points_to_display);
		// this.hadmGraphAverageEachNPointFormControl.setValue(this.configuration.general.hadm_graph_parameters.average_each_n_point);
		// this.hadmGraphScaleStepFormControl.setValue(this.configuration.general.hadm_graph_parameters.scale.step);

		this.aoaGraphNumberOfPointsToDisplayFormControl.setValue(this.configuration.general.aoa_graph_parameters.number_of_points_to_display);
		this.aoaGraphAverageEachNPointFormControl.setValue(this.configuration.general.aoa_graph_parameters.average_each_n_point);
		this.aoaGraphScaleStepFormControl.setValue(this.configuration.general.aoa_graph_parameters.scale.step);
		this.aoaBarGraphScaleStepFormControl.setValue(this.configuration.general.aoa_bar_graph_parameters.scale.step);

		this.cc26x2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0x0f;
		this.cc26x2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc26x2.aoa_sampling_control & 0xf0;
		this.cc2640r2SelectedAoaSamplingFilter = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0x0f;
		this.cc2640r2SelectedAoaSamplingAntenna = this.configuration.aoa_parameters.aoa_cc2640r2.aoa_sampling_control & 0xf0;

		this.clAoaSyncTimeoutFormControl.setValue(this.configuration.cl_aoa_parameters.cl_aoa_scanner.sync_timeout * 10);
		this.clAoaSelectedAoaSamplingFilter = this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_sampling_control & 0x0f;
		this.clAoaSelectedAoaSamplingAntenna = this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_sampling_control & 0xf0;
	}

	XOR(a: boolean, b: boolean): boolean {
		return (a || b) && !(a && b);
	}

	updateColor(color, index) {
		this.configuration.general.color_schema[index] = color;
	}

	aoaRawModeChanged(event) {
		if (event === this.AOA_MODE_ANGLE) {
			this.cc26x2SelectedAoaSamplingFilter = 0;
			this.cc2640r2SelectedAoaSamplingFilter = 0;

			this.aoaSamplingAntennaChanged(this.cc26x2SelectedAoaSamplingAntenna);
		}

		if (event === this.AOA_MODE_RAW) {
			if (this.configuration.aoa_parameters.aoa_cc26x2.aoa_slot_durations < 2) {
				this.configuration.aoa_parameters.aoa_cc26x2.aoa_slot_durations = 2;
			}

			this.cc26x2SelectedAoaSamplingFilter = 1;
			this.cc2640r2SelectedAoaSamplingFilter = 1;
		}
	}

	aoaSamplingAntennaChanged(event) {
		if (event === 16) {
			/* Ant 1 Only */
			this.configuration.aoa_parameters.aoa_cc26x2.aoa_ant_pattern = [0, 1, 2];
		}

		if (event === 32) {
			/* Ant 2 Only */
			this.configuration.aoa_parameters.aoa_cc26x2.aoa_ant_pattern = [3, 4, 5];
		}

		this.configuration.aoa_parameters.aoa_cc26x2.aoa_pattern_len = this.configuration.aoa_parameters.aoa_cc26x2.aoa_ant_pattern.length;

	}

	aoaAntennaPatternChanged(event) {
		this.configuration.aoa_parameters.aoa_cc26x2.aoa_pattern_len = event.length;
	}

	clAoaRawModeChanged(event) {
		if (event === this.AOA_MODE_ANGLE) {
			this.clAoaSelectedAoaSamplingFilter = 0;

			this.clAoaSamplingAntennaChanged(this.clAoaSelectedAoaSamplingAntenna);
		}

		if (event === this.AOA_MODE_RAW) {
			if (this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_slot_durations < 2) {
				this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_slot_durations = 2;
			}

			this.clAoaSelectedAoaSamplingFilter = 1;
		}
	}

	clAoaSamplingAntennaChanged(event) {
		if (event === 16) {
			/* Ant 1 Only */
			this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_ant_pattern = [0, 1, 2];
		}

		if (event === 32) {
			/* Ant 2 Only */
			this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_ant_pattern = [3, 4, 5];
		}

		this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_pattern_len = this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_ant_pattern.length;

	}

	cl_aoa_select_channels(start: number, stop: number) {
		if (start !== -1 && stop !== -1) {
			this.configuration.cl_aoa_parameters.cl_aoa_slave_by_angle_chart_params.channels = Array<string>(stop - start + 1).fill("").map((v, i) => (i + start).toString());
		} else {
			this.configuration.cl_aoa_parameters.cl_aoa_slave_by_angle_chart_params.channels = [];
		}
	}

	clAoaAntennaPatternChanged(event) {
		this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_pattern_len = this.configuration.cl_aoa_parameters.cl_aoa_config.cl_aoa_ant_pattern.length;
	}
}
