import { Injectable, OnInit } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { Slave } from "app/interfaces/slave";
import { ConfigurationService } from './configuration.service';
import { Configuration } from 'app/interfaces/configuration';

@Injectable({
	providedIn: "root"
})
export class SlaveListService implements OnInit {
	public slaveList: BehaviorSubject<Slave[]> = new BehaviorSubject<Slave[]>([]);
	public periodicAdvSlaveList: BehaviorSubject<Slave[]> = new BehaviorSubject<Slave[]>([]);

	public isAnySlaveConnected: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnySlaveSynced: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnySlaveCciStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnySlaveAoaStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnySlaveHadmStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnySlavePadvReportStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnySlaveClAoaStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

	constructor(
	) { }

	ngOnInit() {
	}

	on_slave_list_change() {
		return this.slaveList.share();
	}

	update_slave_list(
		slave_list: Slave[],
		previous_connected_slave_list?: Slave[],
		previous_synced_slave_list?: Slave[],
		current_periodic_adv_list?: Slave[]
	) {
		if (previous_connected_slave_list.length > 0) {
			previous_connected_slave_list.forEach(conn_slave => {
				// Filtering out all slave that already connected in scan list
				slave_list = slave_list.filter(s => s.bd_address !== conn_slave.bd_address)
			})

			slave_list.push(...previous_connected_slave_list);
		}

		if (previous_synced_slave_list.length > 0) {
			previous_synced_slave_list.forEach(synced_slave => {
				// Filtering out all slave that already synced in scan list
				slave_list = slave_list.filter(s => s.bd_address !== synced_slave.bd_address)
			})

			slave_list.push(...previous_synced_slave_list);
		}

		if (current_periodic_adv_list.length > 0) {
			current_periodic_adv_list.forEach(padv_list_slave => {
				// Filtering out all slave that already synced in periodic advetizer list
				slave_list = slave_list.filter(s => s.bd_address !== padv_list_slave.bd_address)
			})
		}

		this.slaveList.next(slave_list);

		this.check_is_any_slave_connected();
		this.check_is_any_slave_cci_started();
		this.check_is_any_slave_hadm_started();
		this.check_is_any_slave_aoa_started();
		this.check_is_any_slave_padv_report_started();
		this.check_is_any_slave_cl_aoa_started();
	}

	get_current_slave_list(): Slave[] {
		return Array.from(this.slaveList.getValue());
	}

	clear_current_slave_list() {
		this.slaveList.next([]);
	}

	// Connection Area

	update_slave_connection_status(slave: Slave, status: any) {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.is_connected = status.success;
				s.connection_handle = status.conn_hdl;
				return;
			}
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_connected();
	}

	get_connected_slaves() {
		return this.slaveList.getValue().filter(s => s.is_connected);
	}

	check_is_any_slave_connected() {
		let connectd_slave = this.get_connected_slaves();
		this.isAnySlaveConnected.next(connectd_slave.length > 0);
	}

	clear_all_slave_connection_status() {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			s.is_connected = false;
		});
		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_connected();
	}

	// End Connection Area

	// Sync Area

	update_slave_sync_status(slave: Slave, status: any) {
		let current_slave_list = this.slaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.is_synced = status.success;
				s.connection_handle = status.conn_hdl;
				if (status.success === false) { s.color = 'white'; }
				return false;
			}
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_synced();
	}

	update_slave_sync_status_by_bd_addr(bd_address: string, is_synced: boolean, conn_hdl: number, padv_report_started?: boolean, color?: string) {
		let current_slave_list = this.slaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.bd_address === bd_address) {
				s.is_synced = is_synced;

				s.connection_handle = conn_hdl;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_synced();
	}

	update_slave_sync_status_by_conn_hdl(conn_hdl: number, is_synced: boolean, padv_report_started?: boolean, color?: string) {
		let current_slave_list = this.slaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.connection_handle === conn_hdl) {
				s.is_synced = is_synced;

				s.connection_handle = is_synced ? conn_hdl : -1;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_synced();
	}

	get_synced_slaves(): Slave[] {
		return [
			...this.slaveList.getValue().filter(s => s.is_synced && s.advSID !== 255),
			...this.periodicAdvSlaveList.getValue().filter(s => s.is_synced && s.advSID !== 255)
		]
	}

	check_is_any_slave_synced() {
		let synced_slaves = this.get_synced_slaves();
		this.isAnySlaveSynced.next(synced_slaves.length > 0);
	}

	clear_all_slave_sync_status() {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			s.is_synced = false;
		});
		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_synced();
	}

	// End of Sync Area

	// View Area

	clear_slave_in_view(slave: Slave) {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			if (s === slave) {
				s.in_view = false;
			}
		});
		this.slaveList.next(current_slave_list);
	}

	clear_all_slave_in_view() {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			s.in_view = false;
		});
		this.slaveList.next(current_slave_list);
	}

	is_any_slave_in_view() {
		return this.slaveList.getValue().filter(s => s.in_view).length > 0;
	}

	set_slave_in_view(slave: Slave) {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			if (s === slave) {
				s.in_view = true;
			} else {
				s.in_view = false;
			}
		});
		this.slaveList.next(current_slave_list);
	}

	get_slave_in_view(): Slave {
		let current_slave_list = this.slaveList.getValue();
		return current_slave_list.find(s => s.in_view);
	}


	// End View Area

	// CCI Area

	update_slave_cci_status(slave: Slave, status: boolean) {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.cci_started = status;
				return;
			}
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_cci_started();
	}

	get_cci_started_slaves() {
		let connectd_slave = this.get_connected_slaves();
		return connectd_slave.filter(s => s.cci_started);
	}

	check_is_any_slave_cci_started() {
		let cci_started_slaves = this.get_cci_started_slaves();
		this.isAnySlaveCciStarted.next(cci_started_slaves.length > 0);
	}

	clear_all_slave_cci_started_status() {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			s.cci_started = false;
		});
		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_cci_started();
	}

	// End CCI Area

	// AoA Area

	update_slave_aoa_status(slave: Slave, status: boolean) {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.aoa_started = status;
				return;
			}
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_aoa_started();
	}

	get_aoa_started_slaves() {
		let connectd_slave = this.get_connected_slaves();
		return connectd_slave.filter(s => s.aoa_started);
	}

	check_is_any_slave_aoa_started() {
		let aoa_started_slaves = this.get_aoa_started_slaves();
		this.isAnySlaveAoaStarted.next(aoa_started_slaves.length > 0);
	}

	clear_all_slave_aoa_started_status() {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			s.aoa_started = false;
		});
		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_aoa_started();
	}

	// End AoA Area

	// HADM Area

	update_slave_hadm_status(slave: Slave, status: boolean) {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.hadm_started = status;
				return;
			}
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_hadm_started();
	}

	get_hadm_started_slaves() {
		let connectd_slave = this.get_connected_slaves();
		return connectd_slave.filter(s => s.hadm_started);
	}

	check_is_any_slave_hadm_started() {
		let hadm_started_slaves = this.get_hadm_started_slaves();
		this.isAnySlaveHadmStarted.next(hadm_started_slaves.length > 0);
	}

	clear_all_slave_hadm_started_status() {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => {
			s.hadm_started = false;
		});
		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_hadm_started();
	}

	// End HADM Area

	// Periodic Advertiser List Area

	on_periodic_adv_slave_list_change() {
		return this.periodicAdvSlaveList.share();
	}

	get_current_periodic_adv_list() {
		return this.periodicAdvSlaveList.getValue();
	}

	update_padvList_slave_sync_status(slave: Slave, status: any) {
		let current_slave_list = this.periodicAdvSlaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.is_synced = status.success;
				s.connection_handle = status.conn_hdl;
				if (status.success === false) { s.color = 'white'; }
				return false;
			}
		});

		this.periodicAdvSlaveList.next(current_slave_list);
		this.check_is_any_slave_synced();
	}

	update_padvList_slave_sync_status_by_bd_addr(bd_address: string, is_synced: boolean, conn_hdl: number, padv_report_started?: boolean, color?: string) {
		let current_slave_list = this.periodicAdvSlaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.bd_address === bd_address) {
				s.is_synced = is_synced;

				s.connection_handle = conn_hdl;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.periodicAdvSlaveList.next(current_slave_list);
		this.check_is_any_slave_synced();
	}

	update_padvList_slave_sync_status_by_conn_hdl(conn_hdl: number, is_synced: boolean, padv_report_started?: boolean, color?: string) {
		let current_slave_list = this.periodicAdvSlaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.connection_handle === conn_hdl) {
				s.is_synced = is_synced;

				s.connection_handle = is_synced ? conn_hdl : -1;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.periodicAdvSlaveList.next(current_slave_list);
		this.check_is_any_slave_synced();
	}

	add_slave_into_periodic_adv_slave_list(slave: Slave) {
		let periodic_adv_slave_list = this.periodicAdvSlaveList.getValue();
		let slave_list = this.slaveList.getValue();

		slave_list = slave_list.filter(s => s.bd_address !== slave.bd_address);
		periodic_adv_slave_list.push(slave);

		this.slaveList.next(slave_list);
		this.periodicAdvSlaveList.next(periodic_adv_slave_list);
	}

	remove_slave_from_periodic_adv_slave_list(slave: Slave) {
		let periodic_adv_slave_list = this.periodicAdvSlaveList.getValue();

		periodic_adv_slave_list = periodic_adv_slave_list.filter(s => s.bd_address !== slave.bd_address);

		this.periodicAdvSlaveList.next(periodic_adv_slave_list);
	}

	clear_periodic_adv_slave_list() {
		this.periodicAdvSlaveList.next([]);
	}

	// End Periodic Advertiser List Area

	// Periodic Advertiser Report

	update_slave_padv_report_status(slave: Slave, status: boolean) {
		let current_slave_list = this.slaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.slaveList.next(current_slave_list);

		let current_padv_list_slave_list = this.periodicAdvSlaveList.getValue();

		current_padv_list_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.periodicAdvSlaveList.next(current_padv_list_slave_list);

		this.check_is_any_slave_padv_report_started();
	}

	update_slave_padv_report_status_by_handle(sync_handle: number, status: boolean) {
		let current_slave_list = this.slaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.connection_handle === sync_handle) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.slaveList.next(current_slave_list);

		let current_padv_list_slave_list = this.periodicAdvSlaveList.getValue();

		current_padv_list_slave_list.forEach(s => {
			if (s.connection_handle === sync_handle) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.periodicAdvSlaveList.next(current_padv_list_slave_list);

		this.check_is_any_slave_padv_report_started();
	}

	get_padv_report_started_slaves() {
		let synced_slave = this.get_synced_slaves();

		return synced_slave.filter(s => s.padv_report_started);
	}

	check_is_any_slave_padv_report_started() {
		let padv_report_started_slaves = this.get_padv_report_started_slaves();

		this.isAnySlavePadvReportStarted.next(padv_report_started_slaves.length > 0);
	}

	clear_all_slave_padv_report_started_status() {
		let current_slave_list = this.slaveList.getValue();
		current_slave_list.forEach(s => { s.padv_report_started = false; });
		this.slaveList.next(current_slave_list);

		let current_padv_list_slave_list = this.periodicAdvSlaveList.getValue();
		current_padv_list_slave_list.forEach(s => { s.padv_report_started = false; });
		this.periodicAdvSlaveList.next(current_padv_list_slave_list);

		this.check_is_any_slave_padv_report_started();
	}

	// End Periodic Advertiser Report

	// CL AoA Area

	update_slave_cl_aoa_status(slave: Slave, status: boolean) {
		let update_done = false;

		let current_slave_list = this.slaveList.getValue();

		current_slave_list.forEach(s => {
			if (s.bd_address === slave.bd_address) {
				s.cl_aoa_started = status;
				update_done = true;
				return false;
			}
		});

		if (update_done) {
			this.slaveList.next(current_slave_list);
		} else {
			let padv_slave_list = this.periodicAdvSlaveList.getValue();

			padv_slave_list.forEach(s => {
				if (s.bd_address === slave.bd_address) {
					s.cl_aoa_started = status;
					update_done = true;
					return false;
				}
			});

			if (update_done) {
				this.periodicAdvSlaveList.next(padv_slave_list);
			}
		}

		this.check_is_any_slave_cl_aoa_started();
	}

	get_cl_aoa_started_slaves() {
		let synced_slave = this.get_synced_slaves();
		return synced_slave.filter(s => s.cl_aoa_started);
	}

	check_is_any_slave_cl_aoa_started() {
		let cl_aoa_started_slaves = this.get_cl_aoa_started_slaves();
		this.isAnySlaveClAoaStarted.next(cl_aoa_started_slaves.length > 0);
	}

	clear_all_slave_cl_aoa_started_status() {
		let current_slave_list = this.slaveList.getValue();

		current_slave_list.forEach(s => {
			s.cl_aoa_started = false;
		});

		this.slaveList.next(current_slave_list);
		this.check_is_any_slave_cl_aoa_started();
	}

	// End CL AoA Area
}
